import React,{ Component } from 'react';
import $ from 'jquery';
import './main_pls.css';

export default class Mainpls extends Component {
    constructor(){
        super();
        this.state = {
            comments: [],
            pls: [
                {
                    avatar:'user1.jpg',
                    name:'爱尚照明电器-王先生',
                    pl:'和他们合作的一直挺不错的。账做的很清晰，我这种不懂的人都可以简单明了的看清楚，可见他们的专业性是非常强的。这帮我省了不少公司琐事，让我更有精力集中于业务方面。也是合作之前与他们有过充分的沟通，包括服务项目和价格方面等。所以后来的合作也是挺顺利的。',
                    img2:'pl_img1.jpg', 
                    img3:'pl_img2.jpg', 
                    img4:'pl_img5.jpg'
                },
                {
                    avatar:'user2.jpg',
                    name:'海逸风传媒-李小姐',
                    pl:'我在选择代理记账公司之前在网上有搜索过一些相关公司，也在官网上咨询过客服。然后我还是决定再去办公地点了解了下。去了一次办公室之后，感觉非常好，工作人员很热情的接待我。我咨询了一些关于代理记账和注册公司的问题，工作人员解释得很详细，后来问了下费用也不贵，比同行普遍收费还要低一些。',
                    img2:'pl_img3.jpg', 
                    img3:'pl_img4.jpg'
                },
                {
                    avatar:'user3.jpg',
                    name:'正光资源-张先生',
                    pl:'对比了很多家，最终还是选择了百优财税。之前合作的无论是收费价格还是专业性方面都要逊色于百优财税。毕竟财务这一块对于每个公司来说都是非常重要的，一定要找专业的、可以信赖的公司帮忙做。整体感觉就是安全、稳定、效率高',
                    img2:'pl_img6.jpg',
                    img3:'pl_img9.jpg',
                    img4:'pl_img10.jpg'
                },
                {
                    avatar:'user4.jpg',
                    name:'匡运电子-李利民',
                    pl:'百优财税挺不错的，员工办事认真负责。给我们做的账都是非常好的，每次看账都是一目了然，记账员真是用心了，看了朋友推荐给我的果然没错，之前还有点不放心老是电话他们，现在已经完全放手让他们做了。对他们认真负责的态度和专业的能力挺放心的，给个赞',
                    img2:'pl_img7.jpg',
                    img3:'pl_img8.jpg'
                },
                {
                    avatar:'user5.jpg',
                    name:'隆晖机械-张玉梅',
                    pl:'这家公司是挺不错的，我在他们那里注册了公司，他们的速度挺快的，而且价格方面还是算性价比比较高的！个人和他们合作比较愉快',
                    img2:'pl_img11.jpg'
                },
                {
                    avatar:'user.jpg',
                    name:'雷士照明-孙卫国',
                    pl:'工作非常认真负责，业务非常熟，问什么解答的都很满意，把我们公司的记账业务交给这样的会计感觉很放心。虽然只是从网上搜到你们开展业务合作的，但是贵公司业务素质优秀的会计让我觉得百优财税这个公司确实专业，看来原先以为办事要找熟人才放心的说法其实并不靠谱。以后不只记账会长期合作，如果有咨询之类的其他业务也会优先想到你们。',
                    img2:'pl_img12.jpg',
                    img3:'pl_img13.jpg',
                },
                {
                    avatar:'user.jpg',
                    name:'东方水晶-袁静雯',
                    pl:'先谈谈个人经历吧。我是一家商标注册公司的老板，学历不高，对税务知识不是很明白，前段时间被告知公司税务异常，公司可能要关门大吉。特别怕，这家公司是我这么多年攒下来，要是没了就完了。后来朋友告诉我可以找税务公司，就在朋友介绍下找了百优财税。起初也没抱多大希望，就是死马当活马医，结果真的解决了，当朋友告知百优财税还可以处理财务时，我就立马把财务也委托给了他们家。以后财务和税务问题我是不用再担心了。最贴心的是他们家不会做捆绑销售，你需要什么就帮你解决什么，自己感觉很舒服。',
                    img2:'pl_img14.jpg',
                    img3:'pl_img15.jpg'
                }
            ]
        }
    }
    componentDidMount() {
        const _this = this;
        const hostname = window.location.hostname;
        // 评论图片change
        $('.pl-list').on('click', '.sm_img', function () {
            $(this).parent().parent().children('.pl_lg_img').children().attr({
                src: $(this).attr('src')
            });
        });
        $('.pl-list').on('click', '.lg_img', function () {
            $(this).attr({
                src: ''
            })
        })
        $.ajax({
            url: 'http://' + hostname + ':3002/comment',
            type: 'get',
            dataType: 'json',
            success: function (res){
                _this.setState({
                    comments: res.message
                })
            }
        })
    }
    render() {
        return(
            <div>
                <h3 style={{marginTop: '80px',textAlign: 'center',fontSize: '34px'}} className="pl">用户评论</h3>
                <ul className="pl-list">
                    {
                        this.state.comments.map(function(v,i) {
                            return(
                                <li key={i}>
                                    <img src={v.avatar} alt="" />
                                    <span>{v.name}</span>
                                    <br />
                                    <br />
                                    <b>评分</b><i></i>
                                    <div className="pl_content" dangerouslySetInnerHTML={{ __html: v.content }} ></div>
                                    <div className="pl_img">
                                        <div className="pl_lg_img">
                                            <img src="" className="lg_img" alt="" />
                                        </div>
                                        <hr style={{margin: '0',borderColor: 'transparent'}} />
                                        <div className="pl_imgs">
                                            {v.pl_img1?<img src={v.pl_img1} alt="img2" className="sm_img" />:''}
                                            {v.pl_img2?<img src={v.pl_img2} alt="img3" className="sm_img" />:''}
                                            {v.pl_img3?<img src={v.pl_img3} alt="img4" className="sm_img" />:''}
                                        </div>
                                    </div>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        )
    }
}


