import React,{ Component } from 'react';
import { NavLink } from 'react-router-dom';
import $ from 'jquery';
import './main_index.css';
import Swiper from '_dirname/../../public/js/swiper.min';
import dingwei from "../images/dingwei.png";
import lou from "../images/lou.png";
import home_icon_register_hhqy from "../images/home_icon_register_hhqy.png";
import fgspng from "../images/fgspng.png";
import nzqy from "../images/nzqy.png";
import grdz from "../images/grdz.png";
import wzqy from "../images/wzqy.png";
import dzbg from "../images/dzbg.png";
import zjbg from "../images/zjbg.png";
import mcbg from "../images/mcbg.png";
import frbg from "../images/frbg.png";
import jyfwbg from "../images/jyfwbg.png";
import zx from "../images/zx.png";
import gqbg from "../images/gqbg.png";
import dxzbg from "../images/dxzbg.png";
import logo112 from "../images/logo112.png";
import why2 from "../images/why2.gif";
import why3 from "../images/why3.gif";
import b30970dc8a44 from "../images/b30970dc8a44.jpg";
import pl_img1 from "../images/pl_img1.jpg";
import feedb38f55e from "../images/feedb38f55e.jpg";
import pl_img2 from "../images/pl_img2.jpg";
import b309712cf854 from "../images/b309712cf854.jpg";
import pl_img14 from "../images/pl_img14.jpg";
import businessteam from "../images/businessteam.jpg";
import pl_img15 from "../images/pl_img15.jpg";
import b309711bfd62 from "../images/b309711bfd62.jpg";
import Mainbanner from './main_banner';

export default class Mainindex extends Component {
    constructor() {
        super();
        this.state = {
            useNum : 3215,
            iniNum : 1
        }
        this.tankuang = this.tankuang.bind(this);
    }
    tankuang() {
		$('.motai').fadeIn();
    }

    componentDidMount() {
        $(document).scrollTop(0);
        let _this = this;

		let swiper2 = new Swiper('.swiper-container2', {
			loop: true,
			autoplay: true,
			delay: 2000,
			slidesPerView: 4,
			spaceBetween: 10,
        });

        // 监听鼠标滚动 加animate文字效果
        let teamTop = $('.team').offset().top,
            ysTop = $('.ys-title').offset().top;
        loadNum();
        function loadNum(){
            var iniNum = _this.state.iniNum,
                useNum = _this.state.useNum;
            if( iniNum >= useNum ) return;
            $('.useNum').text(iniNum);
            iniNum += 21;
            _this.setState({
                iniNum: iniNum
            })
        }
        this.timer = setInterval(()=>{
            var cTop = $(document).scrollTop() + $(window).innerHeight();
            if (cTop > ysTop) {
                loadNum();
            }
        },30);
        $(document).scroll(function () {
            var cTop = $(document).scrollTop() + $(window).innerHeight();
            if (cTop > teamTop) {
                $('.team').addClass('animated fadeInLeft');
            }
        });
    }
    componentWillUnmount() {
        clearInterval(this.timer);
    }
    render() {
        return(
            <div id="main_index">
                <Mainbanner />
                <div className="yewu">
                    <div className="yw1">
                        <div className="yewu-wrapper">
                            <div className="zhuce">
                                <div className="container">
                                    <div className="row clear">
                                        <div className="zhuce-title col-md-4 col-sm-12">
                                            <h3>注册公司</h3>
                                            <p>开公司起个独一无二的名字很重要</p>
                                        </div>
                                        <div className="zhuce-dizhi col-md-4 col-sm-12 clear">
                                            <div className="left">
                                                <NavLink to="/gsfw">
                                                    <img src={dingwei} alt="dingwei" />
                                                    <p>了解详情</p>
                                                </NavLink>
                                            </div>
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsfw?item=0">
                                                        <img src={lou} alt="lou" />
                                                        <p>有限公司</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsfw?item=1">
                                                        <img src={home_icon_register_hhqy} alt="home_icon_register_hhqy" />
                                                        <p>合伙企业</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="zhuce-fl col-md-4 col-sm-12 clear">
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsfw?item=2">
                                                        <img src={fgspng} alt="fgspng" />
                                                        <p>分公司注册</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsfw?item=4">
                                                        <img src={nzqy} alt="nzqy" />
                                                        <p>内资企业</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsfw?item=3">
                                                        <img src={grdz} alt="grdz" />
                                                        <p>个人独资</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsfw?item=5">
                                                        <img src={wzqy} alt="wzqy" />
                                                        <p>外资企业</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="yw2">
                        <div className="yewu-wrapper">
                            <div className="zhuce jizhang">
                                <div className="container">
                                    <div className="row clear">
                                        <div className="zhuce-title jizhang-title col-md-4 col-sm-12">
                                            <h3>记账报税</h3>
                                            <p>公司开始经营，就要记账报税啦</p>
                                        </div>
                                        <div className="zhuce-dizhi jizhang-dt col-md-4 col-sm-12 clear">
                                            <span>
                                                <NavLink to="/caishui?item=0">小规模</NavLink>
                                                <p>税点低(3%)，做账简单</p>
                                                <p>不能开专用发票</p>
                                                <p>如开专票，需税局代开</p>
                                                <p>年收入有上限</p>
                                                <p>小规模公司可直接申请一般纳税人</p>
                                            </span>
                                        </div>
                                        <div className="zhuce-dizhi jizhang-dt col-md-4 col-sm-12 clear">
                                            <span>
                                                <NavLink to="/caishui?item=1">一般纳税人</NavLink>
                                                <p>税点高(16%)，做账比较麻烦</p>
                                                <p>可以开专用发票</p>
                                                <p>可以做进项抵扣</p>
                                                <p>年收入没有上限</p>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="yw3">
                        <div className="yewu-wrapper">
                            <div className="zhuce">
                                <div className="container">
                                    <div className="row clear">
                                        <div className="zhuce-title col-md-4 col-sm-12">
                                            <h3>企业变更</h3>
                                            <p>信息发生变化，要记得做变更</p>
                                        </div>
                                        <div className="zhuce-fl col-md-4 col-sm-12 clear">
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsbg">
                                                        <img src={dzbg} alt="dzbg" />
                                                        <p>变更</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsbg?item=1">
                                                        <img src={zjbg} alt="zjbg" />
                                                        <p>资金变更</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsbg?item=0">
                                                        <img src={mcbg} alt="mcbg" />
                                                        <p>名称变更</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsbg?item=5">
                                                        <img src={frbg} alt="frbg" />
                                                        <p>法人变更</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="zhuce-fl col-md-4 col-sm-12 clear">
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsbg?item=3">
                                                        <img src={jyfwbg} alt="jyfwbg" />
                                                        <p>经营范围变更</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsbg?item=4">
                                                        <img src={zx} alt="zx" />
                                                        <p>注销</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsbg?item=2">
                                                        <img src={gqbg} alt="gqbg" />
                                                        <p>股权变更</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                            <div className="left">
                                                <div>
                                                    <NavLink to="/gsbg">
                                                        <img src={dxzbg} alt="dxzbg" />
                                                        <p>吊销转注销</p>
                                                    </NavLink>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="ys">
                    <div className="ys-wrapper">
                        <div className="ys-title">
                            <h2>
                                <span className="useNum">0</span> 家企业
                                <br className="huanhang" />正在使用百优财税
                            </h2>
                        </div>
                    </div>
                    <div className="ys-dt-wrapper">
                        <div className="ys-dt">
                            <div className="container">
                                <div className="row">
                                    <div className="col-md-6 ys-dt-tittle">
                                        <h3>选择百优财税的理由</h3>
                                        <div className="liyou">
                                            <p>一、 专注专业的服务定位</p>
                                            <span>专注于工商、财务服务，因为专注 • 所以专业</span>
                                        </div>
                                        <div className="liyou">
                                            <p>二、 强大专业的会计团队</p>
                                            <span>专业会计师、税务师为您一对一服务</span>
                                        </div>
                                        <div className="liyou">
                                            <p>三、 丰富的实践经验</p>
                                            <span>多年工商税务办理经验，合作超过三千家公司</span>
                                        </div>
                                        <div className="liyou">
                                            <p>四、 高效的售后服务</p>
                                            <span>解决各种日常工商服务问题，为企业答忧解惑</span>
                                        </div>
                                        <a onClick={this.tankuang}>立即联系</a>
                                    </div>
                                    <div className="col-md-6 ys-img">
                                        <img src={logo112} className="our_logo" alt="our_logo" />
                                        <div className="quan"></div>
                                        <div className="huan"></div>
                                        <div className="quan-sm quan-sm1">公司注册</div>
                                        <div className="quan-sm quan-sm2">分公司注册</div>
                                        <div className="quan-sm quan-sm3">财税筹划</div>
                                        <div className="quan-sm quan-sm4">记账报税</div>
                                        <div className="quan-sm quan-sm5">名称变更</div>
                                        <div className="quan-sm quan-sm6">法人变更</div>
                                        <div className="quan-sm quan-sm7">股权注销</div>
                                        <div className="quan-sm quan-sm8">注销公司</div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-6 gif gif1">
                                        <div>
                                            <img src={why2} alt="why2" />
                                            <h4>百优使公司注册更简单</h4>
                                            <p>百优提供可以注册公司的地址-免费在线填写您的信息，制作文档无误，专人贴心客服，足不出户完成公司注册</p>
                                        </div>
                                    </div>
                                    <div className="col-md-6 gif gif2">
                                        <div>
                                            <img src={why3} alt="why3" />
                                            <h4>多项风控 招商更安全</h4>
                                            <p>人脸活体验证 | 第三方征信筛选 | 手机号码实名验证 | 多维度检测保证招商安全</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="fengcai">
                    <div className="fc-wrapper">
                        <h2 className="team">高效率、精细化团队</h2>
                        <div className="swiper-container2">
                            <div className="swiper-wrapper">
                                <div className="swiper-slide">
                                    <img src={b30970dc8a44} alt="b30970dc8a44" />
                                </div>
                                <div className="swiper-slide">
                                    <img src={pl_img1} alt="pl_img1" />
                                </div>
                                <div className="swiper-slide">
                                    <img src={feedb38f55e} alt="feedb38f55e" />
                                </div>
                                <div className="swiper-slide">
                                    <img src={pl_img2} alt="pl_img2" />
                                </div>
                                <div className="swiper-slide">
                                    <img src={b309712cf854} alt="b309712cf854" />
                                </div>
                                <div className="swiper-slide">
                                    <img src={pl_img14} alt="pl_img14" />
                                </div>
                                <div className="swiper-slide">
                                    <img src={businessteam} alt="businessteam" />
                                </div>
                                <div className="swiper-slide">
                                    <img src={pl_img15} alt="pl_img15" />
                                </div>
                                <div className="swiper-slide">
                                    <img src={b309711bfd62} alt="b309711bfd62" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


