import React,{ Component } from 'react';
import $ from 'jquery';
import './main_mianhe.css';
import liyang from '../images/liyang.jpg';

export default class Mainmianhe extends Component {
    constructor() {
        super();
    }
    componentDidMount(){
        const _this = this;
        // 城市选择查询核名
        $('.choose-city>span').on('click',function(e){
            if(!$(this).children('i').hasClass('rota')){
                $(this).children('i').addClass('rota');
                $(this).next().fadeIn();
            }else {
                $(this).children('i').removeClass('rota');
                $(this).next().fadeOut();
            }
            e.stopPropagation();
        });
        $(document).on('click',function(){
            if($('.choose-city>span>i').hasClass('rota')){
                $('.choose-city>span>i').removeClass('rota');
                $('.choose-city>span').next().fadeOut();
            }
        });
        $('.choose-city ul li').on('click',function(){
            $('.choose-city>span>b').text($(this).text());
        });

        // 定义弹出框
        $('.start_heming').on('click',function(){
            $('.chaxunye').css({
                display: 'block'
            });
            $('.fankuiye').css({
                display: 'none'
            });
            $('.getphone').show();
            $('.getphone-inner').slideDown(300);
        });
        $('.getphone,.getphone-inner .shut').on('click',function(){
            $('.getphone').hide();
            $('.getphone-inner').hide();            
        });
        $('.getphone-inner').on('click',function(e){
            e.stopPropagation();
        });
        $('.fasong').on('click',function(){
            var phone_reg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/,
                phonenumber = $('.phonenumber').val(),
                qiyemingcheng = $('.qiyemingcheng').val(),
                lianxiren = $('.lianxiren').val();

            if(!phone_reg.test(phonenumber)){
                alert('请填写正确的手机号码');
            }else {
                _this.currentAjax = $.ajax({
                    url: './get_phone.php',
                    type: 'post',
                    data: {
                        qiyemingcheng: qiyemingcheng,
                        phonenumber: phonenumber,
                        lianxiren: lianxiren
                    },
                    success: function(){
                        console.log('success');
                    },
                    error: function(err){
                        console.log(err);
                    }
                });
                $('.chaxunye').css({
                    display: 'none'
                });
                $('.fankuiye').css({
                    display: 'block'
                });
                return false;
            }
        });
        // 城市选择核名 结束
    }
    componentWillUnmount(){
        if(this.currentAjax) this.currentAjax.abort();
    }
    render(){
        return(
            <div>
                <div className="mianhe">
                    <div className="mianhe-inner clear">
                        <div className="choose-city left">
                            <span>
                                <b>连云区</b>
                                <i className="fa fa-angle-down" style={{fontSize: '18px',color: '#E95204',transition: '.5s',transformOrigin: 'center center',textIndent: '0'}}></i>
                            </span>
                            <ul>
                                <span id="sjx"></span>
                                <li>连云区</li>
                                <li>海州区</li>
                                <li>赣榆区</li>
                                <li>东海县</li>
                                <li>灌云县</li>
                                <li>灌南县</li>
                            </ul>
                        </div>
                        <div className="company left">
                            <input type="text" placeholder="请输入查询公司名称，例 ：连云港百优信息科技有限公司" />
                        </div>
                        <div className="chaxun left start_heming">查询能否注册</div>
                    </div>
                </div>
                <div className="getphone" style={{display: 'none'}}>
                    <div className="getphone-inner" style={{display: 'none'}}>
                        <span className="shut"></span>
                        <div className="chaxunye">
                            <h3>企业名免费查询</h3>
                            <p>专业顾问人工查询，30分钟内精准回复</p>
                            <form action="./get_phone.php" method="POST" id="fillphone">
                                <p>
                                    <label>企业名称：</label>
                                    <input type="text" className="qiyemingcheng" name="qiyemingcheng" id="qymc" placeholder="请输入查询公司的名称，例：连云港百优信息科技有限公司" />
                                </p>
                                <p>
                                    <label>
                                        <i style={{color: 'crimson',fonSize: '14px'}}>*</i> 手机号码：</label>
                                    <input type="text" className="phonenumber" name="phonenumber" id="dhhm" placeholder="方便接收查询结果，仅官方可见" required />
                                </p>
                                <p>
                                    <label>联系人：</label>
                                    <input type="text" className="lianxiren" name="lianxiren" id="mz" placeholder="如：王先生" />
                                </p>
                                <div>
                                    <input type="button" value="获取查询结果" className="fasong" />
                                </div>
                            </form>
                        </div>
                        <div className="fankuiye" style={{display: 'none'}}>
                            <h3>您的申请我们已经收到</h3>
                            <p>请耐心等待，财税顾问会在30分钟内联系您</p>
                            <div className="clear">
                                <div className="guanzhu left">
                                    <img src={liyang} alt="liyang" />
                                    <p>扫描二维码咨询财务专家</p>
                                </div>
                                <div className="fengexian left">
                                    <p></p>
                                </div>
                                <div className="qukankan left">
                                    <h4>去别处看看</h4>
                                    <ul>
                                        <li>
                                            <a href="/gsfw">公司注册</a>
                                        </li>
                                        <li>
                                            <a href="/caishui">代理记账</a>
                                        </li>
                                        <li>
                                            <a href="/gsbg">公司变更</a>
                                        </li>
                                        <li>
                                            <a href="/wenti">常见问题</a>
                                        </li>
                                        <li>
                                            <a href="/women">关于我们</a>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


