import React,{ Component } from 'react';
import $ from 'jquery';
import './main_gsfw.css';
import ddd from '../images/ddd.jpg';
import reg from '../images/1530585080494081.jpg';
import banImg from '../images/1530585211475631.jpg';
import Mainpls from './main_pls';
import Mainpromise from './main_promise';
import Mainzixun from './main_zixun';
import Mainmianhe from './main_mianhe';

export default class Maingsfw extends Component {
    constructor() {
        super()
        this.state = {
            which_yewu: 0,
            zl_lists: [
                [
                    '1. 公司设立登记申请书',
                    '2. 股东的主体资格证明或者自然人身份证件复印件',
                    '3. 住所使用证明；全体股东签署的公司章程',
                    '4. 依法设立的验资机构出具的验资证明',
                    '5. 以股权出资的，提交《股权认缴出资承诺书》',
                    '6. 董事、监事和经理的任职文件及身份证件复印件'
                ],
                [
                    '1. 企业设立登记申请书',
                    '2. 全体合伙人的身份证明或主体资格证明，合伙协议',
                    '3. 全体合伙人对各合伙人认缴或者实际缴付出资的确认书',
                    '4. 企业名称预先核准通知书及预先核准名称投资人名录表',
                    '5. 合伙人为外商投资企业（不含外商投资的投资性公司），合伙企业申请的经营范围涉及《外商投资产业指导目录》中限制类的，还应提交商务部门的批准文件',
                    '6. 经营范围涉及前置许可项目的，应提交有关审批部门的批准文件'
                ],
                [
                    '1. 公司法定代表人签署的《分公司设立登记申请书》(公司加盖公章)',
                    '2. 公司签署的《指定代表或者共同委托代理人的证明》(公司加盖公章)及指定代表或委托代理人的身份证复印件(本人签字)。应标明具体委托事项、被委托人的权限、委托期限',
                    '3. 公司章程(由公司法定代表人签署)',
                    '4. 公司营业执照副本的复印件',
                    '5. 分公司营业场所使用证明',
                    '6. 公司出具的分公司负责人的任职文件及身份证明复印件',
                    '7. 分公司申请登记的经营范围中有法律、行政法规和国务院决定规定必须在登记前报经批准的项目，提交有关的批准文件或者许可证书复印件或许可证明；分公司的经营范围不得超出公司的经营范围',
                    '8. 法律、行政法规和国务院决定规定设立分公司必须报经批准的，提交有关的批准文件或者许可证书复印件'
                ],
                [
                    '1. 投资人签署的《个人独资企业登记(备案)申请书》',
                    '2. 投资人身份证明复印件（居民身份证或户籍证明）',
                    '3. 投资人委托代理人的，应当提交投资人的委托书原件和代理人的身份证明或资格证明复印件(核对原件)',
                    '4. 企业住所证明',
                    '5. 从事法律、行政法规规定须报经有关部门审批的业务的，应当提交有关部门的批准文件',
                    '6. 国家工商行政管理总局规定提交的其他文件'
                ],
                [
                    '1. 企业设立登记申请书',
                    '2. 企业公司章程',
                    '3. 企业国有资产产权登记表，集体所有制企业提交验资报告',
                    '4. 主管部门或审批机关的批准文件',
                    '5. 注册地址使用证明，包括住所租赁使用证明、住所产权归属证明',
                    '6. 法定代表人履历表及身份证复印件'
                ],
                [
                    '1. 承诺书、法律文件送达委托书',
                    '2. 办理人员授权委托书、办理人员身份证复印件',
                    '3. 设立外资企业申请书、设立行政许可申请表(投资方签字)',
                    '4. 可行性分析报告(包括文字及经济部分，由投资方签字盖章)',
                    '5. （1）投资外方是公司，需商业登记证、法定代表人有效证明文件，及资信证明。公司成立满一年以上，需要提供审计报告原件',
                    '（2）法定代表人为大陆提供身份证原件；若是香港人需提供港澳通行证及香港永久身份证（需公证认证），以上证明需经所在国家公证机关公证并经我国驻该国使领馆认证，港澳台仅需提供当地公证机构的公证文件',
                    '6. 房屋、场地租赁协议及产权证明复印件、章程中文正式文本（全体投资方签字）',
                    '7. 商业企业需提供以下文件：',
                    '（1）进出口商品目录（经营范围有进出口时提供）',
                    '（2）投资方为公司并成立一年以上需提供上一年度审计报告',
                    '8. 以上文件如有外文需翻译公司翻译，并在翻译件上加盖翻译公司公章，提供翻译公司营业执照复印件加盖翻译公司公章'
                ]
            ]
        }
        this.tankuang = this.tankuang.bind(this);
    }
    tankuang() {
        $('.motai').fadeIn();
    }
    componentWillMount() {
        const item = parseInt(window.location.hash.substr(12))?parseInt(window.location.hash.substr(12)):0;
        this.setState({
            which_yewu: item
        })
    }
    componentDidMount() {
        const _this=this;
        const item = this.state.which_yewu;
        if(item) { 
            $(document).scrollTop($('.ziliao').offset().top-80);
            $('.items li').removeClass('chooseCol');
            $('.items li').eq(item).addClass('chooseCol');
            $('.items h3').text($('.items li').eq(item).text());
        }else {
            $(document).scrollTop(0);
        }

        $('.item-lists li').on('click', function () {
            $('.items li').removeClass('chooseCol');
            $(this).addClass('chooseCol');
            $('.items h3').text($(this).text());
            _this.setState({
                which_yewu: $(this).index()
            })
            $('html,body').stop().animate({
                scrollTop: $('.ziliao').offset().top-80
            },150);
        });
    }
    render() {
        return(
            <div className="main_gsfw">
                <div className="list">
                    <div className="list-wrapper clear">
                        <div className="listImg left">
                            <img src={ddd} alt="ddd" />
                        </div>
                        <div className="items left">
                            <h3>有限公司</h3>
                            <p>价格：因区域不同请咨询在线客服</p>
                            <div className="label">选择类型:</div>
                            <ul className="item-lists">
                                <li className="chooseCol">有限公司</li>
                                <li>合伙企业</li>
                                <li>分公司注册</li>
                                <li>个人独资</li>
                                <li>内资企业</li>
                                <li>外资企业</li>
                            </ul>
                            <a style={{whiteSpace: 'nowrap',overflow: 'hidden',textOverflow: 'ellipsis'}} onClick={this.tankuang}>电话咨询</a>
                        </div>
                    </div>
                    <div className="prog">
                        <Mainmianhe />
                        <h2>详细流程</h2>
                        <p style={{fontSize: '20px',fontWeight: '600',color: '#394a51',marginTop: '12px',paddingLeft:'20px'}}>我们理解各位老板在创业初期遇到的疑惑及困难，故本公司提供免费工商政策、财税政策咨询服务。</p>
                        <p style={{fontSize: '20px',fontWeight: '600',marginTop: '12px',color: '#f73859',paddingLeft:'20px'}}>本公司承诺：任何收费合理合规，无任何隐性收费！</p>
                        <h3 style={{marginTop: '40px',textAlign: 'center',fontSize: '24px',fontWeight: 'bold',color: '#f00'}}> 公司业务：</h3>
                        <p style={{fontSize: '18px',marginTop: '40px',fontWeight: 'bold',paddingLeft:'20px'}}>各类型 连云港及江苏开头<span style={{color: '#f00'}}>公司注册、执照变更、注销、老公司出售</span>，个体营业执照代办，专业会计<span style={{color: 'rgb(0, 176, 240)'}}>代理记账、建账、年报</span>，工商税务注销及疑难处理等</p>
                        <h3 style={{fontSize: '24px',fontWeight: 'bold',color: 'rgb(255, 0, 0)',paddingLeft:'20px'}}>一站式服务全程办理！</h3>
                        <h3 style={{fontSize: '24px',fontWeight: 'bold',color: 'rgb(112, 48, 160)',textAlign: 'center'}}>服务范围：</h3>
                        <p style={{marginTop:'40px',fontSize: '20px',fontWeight: 'bold',paddingLeft:'20px'}}>连云港全境：新浦 海州、连云  开发区、赣榆  东海  灌云  灌南</p>
                        <p style={{marginTop:'40px',fontWeight: 'bold',fontSize: '18px',paddingLeft:'20px'}}><span style={{fontSize: '24px'}}>快速咨询：<a href="tel:18052310959" style={{color: '#f00',textDecoration: 'none'}}>18052310959  李经理</a>（微信同号）,</span>欢迎免费咨询<b style={{color: '#f00'}}>工商政策、财税政策</b>等问题！</p>
                        <p style={{marginTop:'40px',fontWeight: 'bold',fontSize: '18px',color: 'rgb(0, 176, 80)',paddingLeft:'20px'}}>公司地址：连云港市海州区朝阳东路32-7号金海财富中心A座503   电话：0518-85772599</p>
                        <h3 style={{marginTop:'100px',color: 'rgb(255, 0, 0)',fontFamily: '宋体, SimSun',fontSize: '24px',fontWeight: 'bold',paddingLeft:'20px'}}>一、公司注册类型说明：</h3>
                        <p style={{marginTop:'40px',fontSize: '18px',lineHeight: '30px',paddingLeft:'20px'}}><span style={{color: 'rgb(192, 0, 0)',fontWeight: 'bold'}}>1、连云港公司注册</span>（3-5工作日出营业执照）：注册以连云港开头的公司，认缴制，无需实缴注册资金，建议20万-200万以内为合理范围！<span style={{color: 'rgb(0, 176, 80)',fontWeight: 'bold'}}>【全程绿色通道 ，一站式解决方案】</span></p>
                        <p style={{marginTop:'40px',fontSize: '18px',lineHeight: '30px',paddingLeft:'20px'}}><span style={{color: 'rgb(192, 0, 0)',fontWeight: 'bold'}}>2、江苏公司注册</span>（4-7工作日出营业执照）：注册以江苏开头的公司，比如：江苏XX商贸有限公司。江苏开头，高大上！认缴制，无需实缴注册资金，注册资本需填写1000万起步；<span style={{color: 'rgb(0, 176, 80)',fontWeight: 'bold'}}>【全程绿色通道 ，一站式解决方案】</span></p>
                        <p style={{marginTop:'40px',fontSize: '18px',lineHeight: '30px',paddingLeft:'20px'}}><span style={{color: 'rgb(192, 0, 0)',fontWeight: 'bold'}}>3、个体营业执照</span>（正常1-2个工作日下营业执照）：仅办理连云港市区执照（新浦、海州、连云）。只需身份证复印件和2寸照片1张即可，本人不用到场（本地人）。<span style={{color: 'rgb(0, 176, 80)',fontWeight: 'bold'}}>【全程绿色通道 ，一站式解决方案】</span></p>
                        <h3 style={{marginTop: '80px',textAlign: 'center',fontSize: '34px'}} className="progdetail">工商注册流程</h3>
                        <img className="regImg" src={reg} alt="reg" style={{margin: '50px auto',display: 'block',width: '100%'}} />
                        <h3 style={{marginTop: '80px',textAlign: 'center',fontSize: '34px'}} className="ziliao">所需资料</h3>
                        <ul style={{marginTop: '50px',fontSize: '18px',color: '#E95204'}} className="ziliaoList">
                            {
                                this.state.zl_lists[this.state.which_yewu].map((v,i)=>{
                                    return(
                                        <li key={i}>{v}</li>
                                    )
                                })
                            }
                        </ul>
                        <h3 style={{marginTop: '80px',textAlign: 'center',fontSize: '34px'}} className="banli">办理流程</h3>
                        <img className="banImg" src={banImg} alt="banImg" style={{width: '80%',margin: '50px auto',display: 'block',borderRight: '1px solid #CBCBCB',borderBottom: '1px solid #cbcbcb'}} />
                        <p style={{margin: '0 20px',lineHeight: '100px',textAlign: 'center',background: 'linear-gradient(to right bottom,#1446EE,#04A3E5,#1446EE)',color: '#fff',fontSize: '20px',constterSpacing: '2px'}}>提供完整资料,专属会计全程跟踪完成,我们竭诚为您服务</p>
                        <Mainpls />
                        <Mainpromise />
                    </div>
                </div>
                <Mainzixun />
            </div>
        )
    }
}


