import React,{ Component } from 'react';
import $ from 'jquery';
import Mainpls from './main_pls';
import Mainpromise from './main_promise';
import Mainzixun from './main_zixun';
import ddd from '../images/ddd.jpg';

export default class Maingsbg extends Component {
    constructor() {
        super()
        this.state = {
            which_yewu: 0,
            zl_lists1 : [
                [
                    '1.《公司变更（改制）登记申请书（公司备案申请表）》',
                    '2.《企业法人营业执照》正、副本',
                    '3.《指定（委托）书》',
                    '4. 住所使用证明、房产证复印件，（产权为个人每页签字；产权人为单位的应加盖公章）',
                    '5. 补充信息登记表'
                ],
                [
                    '1. 公司法定代表人签署的《公司变更登记申请书》',
                    '2. 公司签署的《公司股东(发起人)出资情况表》',
                    '3. 公司签署的《指定代表或者共同委托代理人的证明》(公司加盖公章)及指定代表或委托代理人的身份证复印件(本人签字);应标明具体委托事项、被委托人的权限、委托期限',
                    '4. 股东会决议（多人）或股东会决定（一人）',
                    '5. 公司章程修正案(公司法定代表人签署)',
                    '6. 依法设立的验资机构出具的验资证明',
                    '7. 股份有限公司以募集方式增加注册资本的还应提交国务院证券监督管理机构的核准文件',
                    '8. 法律、行政法规和国务院决定规定变更注册资本必须报经批准的，提交有关的批准文件或者许可证书复印件',
                    '9. 公司减少注册资本的，提交刊登减资公告的报纸报样',
                    '10. 公司营业执照副本'
                ],
                [
                    '1. 法定代表人签署的《企业变更（备案）登记申请书》（原件依份）',
                    '2. 经办人身份证明',
                    '3. 公司关于变更事项的决议或决定',
                    '4. 修改后的公司章程（由股东签署,加盖公章）或者公司章程修正案（由公司法定代表人签署,加盖公章）',
                    '5. 经公证、见证机构公证或见证的股权转让协议，划转股权的提交有权审批部门的划转文件',
                    '6. 向原股东以外的人转让的，提交新股东的主体资格证明',
                    '7. 因股权转让而变更为一人（自然人）独资有限公司的，应提交一人有限公司承诺书'
                ],
                [
                    '1. 《公司变更（改制）登记申请书（公司备案申请书）》',
                    '2. 《企业法人营业执照》正、副本',
                    '3. 《指定（委托）书》',
                    '4. 住所使用证明、房产证复印件，（产权为个人每页签字；产权人为单位的应加盖公章）',
                    '5. 补充信息登记表'
                ],
                [
                    '1. 小规模纳税人及一般纳税人企业（有税控设备及发票的需先缴销发票和设备，缴销表格留存注销时递交所需资料）',
                    '2. 签订三方协议的先终止协议（到税务所领取表格后去银行终止，终止协议需最后注销时递交材料）',
                    '3. 《注销税务登记申请审批表》、国税',
                    '4. 《纳税清算申报表》、《注销税务登记申请审批表》、地税',
                    '5. 税务登记证正、副本原件及复印件',
                    '6. 营业执照副本复印件（工商吊销的企业提供吊销证明）',
                    '7. 股东会决议（有公司股份的企业的需盖公司公章、个人股的需签字）',
                    '8. 注销申请（公司基本情况、成立至今的经营收入及缴税情况、注销公司原因）',
                    '9. 公章',
                    '10. 房屋租赁合同及发票（成立至今）',
                    '11. 审计清算报告（近三年）'
                ],
                [
                    '1. 《公司变更（改制）登记申请书（公司备案申请书）》',
                    '2. 《企业法人营业执照》正、副本',
                    '3. 《指定（委托）书》',
                    '4. 股东会决议（多人）或股东会决定（一人）',
                    '5. 执行董事决定（经理担任法人），若设董事会，董事长担任法人。需要董事会决议（选举、免除与章程一致的法定代表人职务）',
                    '6. 补充信息登记表'
                ]
            ],
            zl_lists2 : [
                [
                    '1. 营业执照正副本原件',
                    '2. 组织机构代码证正副本原件',
                    '3. 税务登记证正副本原件',
                    '4. 统计证正副本原件',
                    '5. 公章',
                    '6. 法人身份证复印件',
                    '7. 可注册公司的商用地址，住所使用证明、房产证复印件，（产权为个人每页签字；产权人为单位的应加盖公章）'
                ],
                [],
                [],
                [
                    '1. 营业执照正副本原件',
                    '2. 组织机构代码证正副本原件',
                    '3. 税务登记证正副本原件',
                    '4. 统计证正副本原件',
                    '5. 公章',
                    '6. 法人身份证复印件'
                ],
                [],
                [
                    '1. 营业执照正副本原件',
                    '2. 组织机构代码证正副本原件',
                    '3. 税务登记证正副本原件',
                    '4. 统计证正副本原件',
                    '5. 公章',
                    '6. 新旧法人及董事、监事、经理身份证复印件'
                ]
            ],
            zl_tt1 : [
                '新公司需要材料',
                '', '',
                '需要提交的文档资料',
                '',
                '需要提交的文档资料'
            ],
            zl_tt2 : [
                '需要的基本资料及信息',
                '', '',
                '需要的基本资料及信息',
                '',
                '需要的基本资料及信息'
            ]
        }
        this.tankuang = this.tankuang.bind(this);
    }
    tankuang() {
        $('.motai').fadeIn();
    }
    componentWillMount() {
        const item = parseInt(window.location.hash.substr(12))?parseInt(window.location.hash.substr(12)):0;
        this.setState({
            which_yewu: item
        })
    }
    componentDidMount(){
        const _this=this;
        const item = this.state.which_yewu;
        if(item) { 
            $(document).scrollTop($('.ziliao').offset().top-80);
            $('.items li').removeClass('chooseCol');
            $('.items li').eq(item).addClass('chooseCol');
            $('.items h3').text($('.items li').eq(item).text());
        }else {
            $(document).scrollTop(0);
        }
        $('.item-lists li').on('click', function () {
            $('.items li').removeClass('chooseCol');
            $(this).addClass('chooseCol');
            $('.items h3').text($(this).text());
            _this.setState({
                which_yewu: $(this).index()
            })
            $('html,body').stop().animate({
                scrollTop: $('.ziliao').offset().top-80
            },150);
        });
    }
    render(){
        return(
            <div className="list">
                <div className="list-wrapper clear">
                    <div className="listImg left">
                        <img src={ddd} alt="ddd" />
                    </div>
                    <div className="items left">
                        <h3>名称变更</h3>
                        <p>价格：因地区不同请咨询在线客服</p>
                        <div className="label">选择类型:</div>
                        <ul className="item-lists">
                            <li className="chooseCol">名称变更</li>
                            <li>资金变更</li>
                            <li>股权变更</li>
                            <li>经营范围变更</li>
                            <li>注销</li>
                            <li>法人变更</li>
                        </ul>
                        <a style={{whiteSpace: 'nowrap',overflow: 'hidden',textOverflow: 'ellipsis'}} onClick={this.tankuang}>电话咨询</a>
                    </div>
                </div>
                <div className="prog">
                    <h2>
                        详细信息
                    </h2>
                    <h3 style={{marginTop: '80px',textAlign: 'center',fontSize: '34px'}} className="ziliao">所需资料</h3>
                    <h4 className="zl-title zl-title1">{this.state.zl_tt1[this.state.which_yewu]}</h4>
                    <ul style={{marginTop: '20px',fontSize: '18px',color: '#E95204'}} className="ziliaoList ziliaoList1">
                        {
                            this.state.zl_lists1[this.state.which_yewu].map((v,i)=>{
                                return(
                                    <li key={i}>{v}</li>
                                )
                            })
                        }
                    </ul>
                    <h4 className="zl-title zl-title2">{this.state.zl_tt2[this.state.which_yewu]}</h4>
                    <ul style={{margin: '20px 0',fontSize: '18px',color: '#E95204'}} className="ziliaoList ziliaoList2">
                        {
                            this.state.zl_lists2[this.state.which_yewu].map((v,i)=>{
                                return (
                                    <li key={i}>{v}</li>
                                )
                            })
                        }
                    </ul>
                    <p className="chengP" style={{margin: '0 20px',lineHeight: '100px',textAlign: 'center',background: 'linear-gradient(to right bottom,#1446EE,#04A3E5,#1446EE)',color: '#fff',fontSize: '20px',letterSpacing: '2px'}}>提供完整资料,专属会计全程跟踪完成,我们竭诚为您服务</p>
                    <Mainpls />
                    <Mainpromise />
                </div>
                <Mainzixun />
            </div>
        )
    }
}


