import React,{ Component } from 'react';
import $ from 'jquery';
import './main_caishui.css';
import ddd from '../images/ddd.jpg';
import ji1 from '../images/1530585436954689.jpg';
import ji2 from '../images/1530585441471427.jpg';
import ji3 from '../images/1530585447265400.jpg';
import ji4 from '../images/1530585453898664.jpg';
import ji5 from '../images/1530585464855055.jpg';
import Mainpls from './main_pls';
import Mainpromise from './main_promise';
import Mainzixun from './main_zixun';

export default class Maincaishui extends Component {
    constructor(){
        super()
        this.state = {
            which_yewu: 0
        }
        this.tankuang = this.tankuang.bind(this);
    }
    tankuang() {
        $('.motai').fadeIn();
    }
    componentWillMount(){
        const item = parseInt(window.location.hash.substr(15))?parseInt(window.location.hash.substr(15)):0;
        this.setState({
            which_yewu: item
        })
    }
    componentDidMount(){
        const item = this.state.which_yewu;
        if(item) {
            $(document).scrollTop($('.ziliao').offset().top-80);
            $('.items li').removeClass('chooseCol');
            $('.items li').eq(item).addClass('chooseCol');
            $('.items h3').text($('.items li').eq(item).text());
        }else {
            $(document).scrollTop(0);
        }

        $('.items li').on('click', function () {
            $('.items li').removeClass('chooseCol');
            $(this).addClass('chooseCol');
            $('.items h3').text($(this).text() + '代理记账');
            $('html,body').stop().animate({
                scrollTop: $('.ziliao').offset().top-80
            },150);
        });
    }
    render(){
        return(
            <div className="list">
                <div className="list-wrapper clear">
                    <div className="listImg left">
                        <img src={ddd} alt="ddd" />
                    </div>
                    <div className="items left">
                        <h3>小规模代理记账</h3>
                        <p>价格：因地区不同请咨询在线客服</p>
                        <div className="label">选择类型:</div>
                        <ul className="item-lists">
                            <li className="chooseCol">小规模</li>
                            <li>一般纳税人</li>
                        </ul>
                        <a style={{whiteSpace: 'nowrap',overflow: 'hidden',textOverflow: 'ellipsis'}} onClick={this.tankuang}>电话咨询</a>
                    </div>
                </div>
                <div className="prog">
                    <h2>
                        详细信息
                    </h2>
                    <h3 style={{marginTop: '80px',textAlign: 'center',fontSize: '34px'}} className="ji">代理记账</h3>
                    <ul className="jiList">
                        <li className="jiItem">
                            <img src={ji1} alt="代理记账" />
                        </li>
                        <li className="jiItem">
                            <img src={ji2} alt="代理记账" />
                        </li>
                        <li className="jiItem">
                            <img src={ji3} alt="代理记账" />
                        </li>
                        <li className="jiItem">
                            <img src={ji4} alt="代理记账" />
                        </li>
                        <li className="jiItem">
                            <img src={ji5} alt="代理记账" />
                        </li>
                    </ul>
                    <h3 style={{marginTop: '80px',textAlign: 'center',fontSize: '34px'}} className="ziliao">所需资料</h3>
                    <h4 className="zl-title">新公司需要材料</h4>
                    <ul style={{marginTop: '20px',fontSize: '18px',color: '#E95204'}} className="ziliaoList">
                        <li>
                            1、原始证照（营业执照、开户行许可证、公章、法人和股东身份证原件）；
                        </li>
                        <li>
                            2、个税明细申报工资表；
                        </li>
                        <li>
                            3、一证通；可证书复印件或许可证明。
                        </li>
                    </ul>
                    <h4 className="zl-title">以往成立公司需要材料</h4>
                    <ul style={{margin: '20px 0',fontSize: '18px',color: '#E95204'}} className="ziliaoList">
                        <li>
                            1、原始证照（营业执照、开户行许可证、公章、法人和股东身份证原件）；
                        </li>
                        <li>
                            2、个税明细申报工资表；
                        </li>
                        <li>
                            3、一证通；
                        </li>
                        <li>
                            4、记账凭证；
                        </li>
                        <li>
                            5、银行对账单；
                        </li>
                        <li>
                            6、总账、明细账；
                        </li>
                        <li>
                            7、税务申报表；
                        </li>
                        <li>
                            8、工商年报、报告书；
                        </li>
                    </ul>
                    <p className="chengP" style={{margin: '0 20px',lineHeight: '100px',textAlign: 'center',background: 'linear-gradient(to right bottom,#1446EE,#04A3E5,#1446EE)',color:' #fff',fontSize: '20px',letterSpacing: '2px'}}>提供完整资料,专属会计全程跟踪完成,我们竭诚为您服务</p>
                    <Mainpls />
                    <Mainpromise />
                </div>
                <Mainzixun />
            </div>
        )
    }
}


